"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Bell, ChevronDown, ChevronRight, Globe, Search, Settings, User, LogOut, Menu } from "lucide-react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"
import { getCurrentUser, logout } from "@/lib/auth"
import { useToast } from "@/hooks/use-toast"

interface NavItem {
  title: string
  icon: React.ReactNode
  href?: string
  children?: NavItem[]
}

interface ErpLayoutProps {
  children: React.ReactNode
  navigation: NavItem[]
}

export function ErpLayout({ children, navigation }: ErpLayoutProps) {
  const [expandedItems, setExpandedItems] = useState<string[]>(["Dashboard"])
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)
  const [activeTooltip, setActiveTooltip] = useState<string | null>(null)
  const [currentUser, setCurrentUser] = useState(getCurrentUser())
  const router = useRouter()
  const { toast } = useToast()

  useEffect(() => {
    const savedColor = localStorage.getItem("theme-color")
    if (savedColor) {
      applyThemeColor(savedColor)
    }
  }, [])

  const applyThemeColor = (color: string) => {
    const hsl = hexToHSL(color)
    document.documentElement.style.setProperty("--primary", `oklch(${hsl.l}% ${hsl.s}% ${hsl.h})`)
  }

  const hexToHSL = (hex: string) => {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex)
    if (!result) return { h: 0, s: 0, l: 0 }

    const r = Number.parseInt(result[1], 16) / 255
    const g = Number.parseInt(result[2], 16) / 255
    const b = Number.parseInt(result[3], 16) / 255

    const max = Math.max(r, g, b)
    const min = Math.min(r, g, b)
    let h = 0
    let s = 0
    const l = (max + min) / 2

    if (max !== min) {
      const d = max - min
      s = l > 0.5 ? d / (2 - max - min) : d / (max + min)
      switch (max) {
        case r:
          h = ((g - b) / d + (g < b ? 6 : 0)) / 6
          break
        case g:
          h = ((b - r) / d + 2) / 6
          break
        case b:
          h = ((r - g) / d + 4) / 6
          break
      }
    }

    return {
      h: Math.round(h * 360),
      s: Math.round(s * 100),
      l: Math.round(l * 100),
    }
  }

  const toggleItem = (title: string) => {
    setExpandedItems((prev) => (prev.includes(title) ? prev.filter((item) => item !== title) : [...prev, title]))
  }

  const handleLogout = () => {
    logout()
    toast({
      title: "Logged Out",
      description: "You have been successfully logged out.",
    })
    router.push("/login")
  }

  return (
    <div className="flex h-screen bg-background text-foreground selection:bg-primary/20">
      {/* Sidebar */}
      <aside
        className={cn(
          "bg-sidebar border-r border-sidebar-border transition-all duration-500 ease-in-out flex flex-col z-50",
          sidebarCollapsed ? "w-20" : "w-72",
        )}
      >
        {/* Logo Section */}
        <div className="p-6 mb-2">
          {!sidebarCollapsed ? (
            <div className="flex items-center gap-4 group cursor-pointer">
              <div className="relative">
                <div className="absolute inset-0 bg-primary/30 blur-xl rounded-full group-hover:bg-primary/50 transition-all duration-500" />
                <div className="relative z-10 p-1 bg-white/10 rounded-xl backdrop-blur-sm border border-white/10 shadow-2xl">
                  <Image
                    src="/images/twist-logo.jpeg"
                    alt="TWIST ERP Logo"
                    width={42}
                    height={42}
                    className="object-contain rounded-lg"
                  />
                </div>
              </div>
              <div className="flex flex-col">
                <span className="text-white font-bold text-lg tracking-tight leading-none">TWIST ERP</span>
                <span className="text-[10px] text-primary font-medium uppercase tracking-widest mt-1">Enterprise</span>
              </div>
            </div>
          ) : (
            <div className="relative flex justify-center group cursor-pointer">
              <div className="absolute inset-0 bg-primary/20 blur-lg rounded-full group-hover:bg-primary/40 transition-all duration-500" />
              <div className="relative z-10 p-1 bg-white/10 rounded-lg backdrop-blur-sm border border-white/10 shadow-xl">
                <Image
                  src="/images/twist-logo.jpeg"
                  alt="TWIST ERP"
                  width={32}
                  height={32}
                  className="object-contain rounded-md"
                />
              </div>
            </div>
          )}
        </div>

        {/* Navigation */}
        <nav className="flex-1 overflow-y-auto px-4 space-y-1.5 scrollbar-hide">
          {navigation.map((item) => (
            <NavItemComponent
              key={item.title}
              item={item}
              expanded={expandedItems.includes(item.title)}
              onToggle={toggleItem}
              collapsed={sidebarCollapsed}
              activeTooltip={activeTooltip}
              setActiveTooltip={setActiveTooltip}
            />
          ))}
        </nav>

        {/* Sidebar Footer */}
        <div className="p-4 mt-auto border-t border-sidebar-border/50">
          <Button
            variant="ghost"
            size="sm"
            className="w-full text-sidebar-foreground/70 hover:text-white hover:bg-white/5 transition-all duration-300 rounded-xl p-2.5"
            onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
          >
            <Menu className={cn("h-5 w-5 transition-transform duration-500", !sidebarCollapsed && "rotate-180")} />
          </Button>
        </div>
      </aside>

      {/* Main Content Area */}
      <div className="flex flex-1 flex-col overflow-hidden relative">
        {/* Top Header */}
        <header className="flex h-20 items-center border-b bg-background/80 backdrop-blur-xl px-8 gap-6 no-print z-40 sticky top-0">
          {/* Global Search Bar */}
          <div className="flex-1 max-w-xl relative group">
            <Search className="absolute left-4 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground group-focus-within:text-primary transition-colors" />
            <Input
              placeholder="Search anything... (⌘K)"
              className="pl-11 h-11 bg-muted/30 border-none focus-visible:ring-2 focus-visible:ring-primary/20 rounded-2xl transition-all duration-300"
            />
          </div>

          <div className="flex items-center gap-3 ml-auto">
            {/* Language Selector */}
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" size="icon" className="h-11 w-11 rounded-xl hover:bg-primary/5 text-muted-foreground hover:text-primary hover:scale-110 active:scale-95 transition-all">
                  <Globe className="h-5 w-5" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-48 rounded-2xl p-2 shadow-premium border-glass bg-glass">
                <DropdownMenuItem className="rounded-xl">English</DropdownMenuItem>
                <DropdownMenuItem className="rounded-xl">العربية</DropdownMenuItem>
                <DropdownMenuItem className="rounded-xl">Français</DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>

            {/* Notifications */}
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" size="icon" className="relative h-11 w-11 rounded-xl hover:bg-primary/5 text-muted-foreground hover:text-primary hover:scale-110 active:scale-95 transition-all">
                  <Bell className="h-5 w-5" />
                  <span className="absolute top-2.5 right-2.5 h-2 w-2 bg-primary rounded-full ring-2 ring-background" />
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-80 rounded-2xl p-4 shadow-premium border-glass bg-glass">
                <div className="flex items-center justify-between mb-4">
                  <DropdownMenuLabel className="p-0 font-bold text-lg">Notifications</DropdownMenuLabel>
                  <Badge variant="outline" className="rounded-full px-2 py-0 text-[10px] font-bold uppercase tracking-wider">3 New</Badge>
                </div>
                <DropdownMenuSeparator className="mb-4" />
                <div className="space-y-3">
                  {[
                    { title: "Approval Required", desc: "Purchase Order #PO-2025-0034", type: "urgent" },
                    { title: "Low Stock Alert", desc: "Cement - 5 bags remaining", type: "warning" },
                    { title: "Maintenance Due", desc: "Vehicle CAT-001 service needed", type: "info" }
                  ].map((n, i) => (
                    <div key={i} className="group p-3 rounded-2xl hover:bg-primary/5 transition-all cursor-pointer border border-transparent hover:border-primary/10">
                      <p className="font-semibold text-sm group-hover:text-primary transition-colors">{n.title}</p>
                      <p className="text-muted-foreground text-xs mt-0.5">{n.desc}</p>
                    </div>
                  ))}
                </div>
                <Button variant="ghost" className="w-full mt-4 text-xs font-bold text-primary hover:bg-primary/5 rounded-xl">View All Notifications</Button>
              </DropdownMenuContent>
            </DropdownMenu>

            {/* Quick Settings */}
            <Button variant="ghost" size="icon" className="h-11 w-11 rounded-xl hover:bg-primary/5 text-muted-foreground hover:text-primary hover:scale-110 active:scale-95 transition-all" asChild>
              <Link href="/company">
                <Settings className="h-5 w-5" />
              </Link>
            </Button>

            <div className="h-8 w-[1px] bg-border mx-2" />

            {/* User Profile Menu */}
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" className="pl-1 pr-3 h-12 rounded-2xl hover:bg-primary/5 hover:scale-[1.02] active:scale-[0.98] transition-all group">
                  <div className="flex items-center gap-3">
                    <div className="h-9 w-9 rounded-xl bg-primary/10 text-primary flex items-center justify-center font-bold shadow-inner group-hover:scale-105 transition-transform duration-300">
                      {currentUser?.name?.charAt(0) || <User className="h-5 w-5" />}
                    </div>
                    <div className="text-left hidden sm:block">
                      <p className="text-sm font-bold leading-tight">{currentUser?.name}</p>
                      <p className="text-[10px] text-muted-foreground font-medium uppercase tracking-wider">{currentUser?.role}</p>
                    </div>
                    <ChevronDown className="h-4 w-4 text-muted-foreground group-hover:text-primary transition-colors" />
                  </div>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end" className="w-64 rounded-2xl p-2 shadow-premium border-glass bg-glass">
                <div className="px-3 py-4 mb-2 bg-primary/5 rounded-xl">
                  <p className="font-bold text-sm">{currentUser?.name}</p>
                  <p className="text-xs text-muted-foreground">{currentUser?.email || "john@example.com"}</p>
                </div>
                <DropdownMenuItem className="rounded-xl gap-3 py-2.5">
                  <User className="h-4 w-4 text-muted-foreground" />
                  <span className="font-medium">My Profile</span>
                </DropdownMenuItem>
                <DropdownMenuItem className="rounded-xl gap-3 py-2.5" asChild>
                  <Link href="/company">
                    <Settings className="h-4 w-4 text-muted-foreground" />
                    <span className="font-medium">Settings</span>
                  </Link>
                </DropdownMenuItem>
                <DropdownMenuSeparator className="my-2" />
                <DropdownMenuItem onClick={handleLogout} className="rounded-xl gap-3 py-2.5 text-destructive focus:bg-destructive/5 focus:text-destructive">
                  <LogOut className="h-4 w-4" />
                  <span className="font-bold">Logout</span>
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </header>

        {/* Main Scrollable Content */}
        <main className="flex-1 overflow-y-auto p-8 bg-[#f8fafc] dark:bg-background/50">
          <div className="max-w-[1600px] mx-auto">
            {children}
          </div>
        </main>
      </div>
    </div>
  )
}

function NavItemComponent({
  item,
  expanded,
  onToggle,
  collapsed,
  depth = 0,
  activeTooltip,
  setActiveTooltip,
}: {
  item: NavItem
  expanded: boolean
  onToggle: (title: string) => void
  collapsed: boolean
  depth?: number
  activeTooltip: string | null
  setActiveTooltip: (title: string | null) => void
}) {
  const hasChildren = item.children && item.children.length > 0
  const showTooltip = activeTooltip === item.title
  const router = useRouter()

  const handleClick = (e: React.MouseEvent) => {
    if (collapsed) {
      if (hasChildren) {
        setActiveTooltip(showTooltip ? null : item.title)
        e.preventDefault()
        e.stopPropagation()
      } else if (item.href) {
        router.push(item.href)
      }
    } else {
      if (hasChildren) {
        onToggle(item.title)
      }
    }
  }

  const content = (
    <>
      <span className={cn(
        "h-5 w-5 flex-shrink-0 transition-transform duration-300 group-hover:scale-110",
        expanded && !collapsed && "text-primary"
      )}>
        {item.icon}
      </span>
      {!collapsed && (
        <>
          <span className="flex-1 text-left font-medium tracking-wide">{item.title}</span>
          {hasChildren && (
            <ChevronRight
              className={cn(
                "h-3.5 w-3.5 transition-all duration-500 text-sidebar-foreground/40 group-hover:text-primary",
                expanded && "rotate-90 text-primary"
              )}
            />
          )}
        </>
      )}
    </>
  )

  const renderTooltip = () => {
    if (!collapsed || !showTooltip) return null

    return (
      <div
        className="fixed left-[88px] px-5 py-4 bg-popover/95 backdrop-blur-xl text-popover-foreground text-sm rounded-2xl shadow-2xl border border-white/10 z-[100] whitespace-nowrap min-w-[220px] max-w-[300px] animate-in fade-in slide-in-from-left-2 duration-300"
        onMouseEnter={() => setActiveTooltip(item.title)}
        onMouseLeave={() => setActiveTooltip(null)}
      >
        <div className="font-bold mb-3 text-base text-primary border-b border-primary/10 pb-2">{item.title}</div>
        {hasChildren && item.children && (
          <div className="space-y-1.5 text-sm">
            {item.children.map((child) => (
              <div key={child.title}>
                {child.href ? (
                  <Link
                    href={child.href}
                    className="flex items-center gap-3 px-3 py-2 rounded-xl hover:bg-primary/10 transition-all text-muted-foreground hover:text-primary group/child"
                    onClick={() => setActiveTooltip(null)}
                  >
                    <span className="h-1.5 w-1.5 rounded-full bg-primary/30 group-hover/child:bg-primary group-hover/child:scale-125 transition-all" />
                    <span className="font-medium">{child.title}</span>
                  </Link>
                ) : (
                  <div className="flex items-center gap-3 px-3 py-2 text-muted-foreground/50">
                    <span className="h-1.5 w-1.5 rounded-full bg-muted" />
                    <span className="font-medium">{child.title}</span>
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
        {!hasChildren && item.href && <div className="text-[10px] uppercase tracking-widest font-bold text-primary/50 mt-2 px-3">Click to navigate</div>}
      </div>
    )
  }

  const baseStyles = cn(
    "w-full flex items-center gap-3 px-4 py-3 rounded-2xl text-sm transition-all duration-300 relative group",
    depth > 0 ? "pl-10 py-2 text-xs text-sidebar-foreground/70" : "text-sidebar-foreground/80",
    collapsed && "justify-center px-0",
    !collapsed && "hover:bg-white/5 hover:text-white",
    expanded && !collapsed && depth === 0 && "bg-white/5 text-white shadow-sm"
  )

  return (
    <div className="relative">
      {item.href && !hasChildren ? (
        <Link
          href={item.href}
          className={baseStyles}
          onMouseEnter={() => collapsed && setActiveTooltip(item.title)}
          onMouseLeave={() => collapsed && setActiveTooltip(null)}
        >
          {content}
          {expanded && !collapsed && depth === 0 && (
            <div className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-6 bg-primary rounded-r-full shadow-[0_0_10px_rgba(var(--primary),0.5)]" />
          )}
        </Link>
      ) : (
        <button
          onClick={handleClick}
          className={cn(baseStyles, showTooltip && collapsed && "bg-white/10 text-white")}
          onMouseEnter={() => collapsed && setActiveTooltip(item.title)}
          onMouseLeave={() => collapsed && !hasChildren && setActiveTooltip(null)}
        >
          {content}
          {expanded && !collapsed && depth === 0 && (
            <div className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-6 bg-primary rounded-r-full shadow-[0_0_10px_rgba(var(--primary),0.5)]" />
          )}
        </button>
      )}

      {renderTooltip()}

      {!collapsed && hasChildren && expanded && (
        <div className="mt-1 space-y-1">
          {item.children?.map((child) => (
            <NavItemComponent
              key={child.title}
              item={child}
              expanded={expanded}
              onToggle={onToggle}
              collapsed={collapsed}
              depth={depth + 1}
              activeTooltip={activeTooltip}
              setActiveTooltip={setActiveTooltip}
            />
          ))}
        </div>
      )}
    </div>
  )
}
